<template>
  <div class="education__item" v-if="item.area">
    <h3 class="education__item__title">{{ item.area }}</h3>
    <div class="education__item__meta">
      <span
        class="education__item__meta__institution"
        v-if="item.institution"
      >
        {{ item.institution }}
      </span>
      <span
        class="education__item__meta__studyType"
        v-if="item.studyType"
      >
        {{ item.studyType }}
      </span>
      <span
        class="education__item__meta__gpa"
        v-if="item.gpa"
      >
        Grade {{ item.gpa }}
      </span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'eduItem',
  props: {
    item: {
      type: Object,
      required: true,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_education.scss";
</style>



