<template>
  <div class="skill__section" v-if="item.name">
    <h3 class="skill__section__title">{{ item.name }}</h3>
    <ul>
      <li v-for="(skill,index) in item.keywords" :key="index">{{ skill }}</li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'skillSection',
  props: {
    item: {
      type: Object,
      required: true,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_skill.scss";
</style>



