<template>
  <div class="work__item" v-if="item.company">
    <h3 class="work__item__title">{{ item.company }}</h3>
    <div class="work__item__meta">
      <span
        class="work__item__meta__position"
        v-if="item.position"
      >
        {{ item.position }}
      </span>
      <span
        class="work__item__meta__start"
        v-if="item.startDate"
      >
        {{ item.startDate }}
      </span>
      <span
        class="work__item__meta__end"
        v-if="item.endDate"
      >
        {{ item.endDate }}
      </span>
    </div>
    <h4 class="work__item__about__title">Roles</h4>
    <ul class="work__item__about">
      <li v-for="(item, index) in item.highlights" :key="index">
        {{item}}
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'workItem',
  props: {
    item: {
      type: Object,
      required: true,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_work.scss";
</style>



