<template>
  <div class="intro">
    <h2 class="intro__title">Introduction</h2>
    <div class="intro__text">
      <div class="intro__text__inner">
        {{intro}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'intro',
  props: {
    intro: {
      type: String,
      required: true,
    },
  },
}
</script>
<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  .intro {
    text-align: left;
    @include mq(tablet){
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    &__title {
      color: getColor(red);
      font-weight: 300;
      font-size: 28px;
    }

    &__text {
      display: block;
      width: 100%;
      @include mq(tablet){
        width: 66%;
        padding-top: 24px;
        &__inner {
          max-width: 310px
        }
      }
    }
  }
</style>





