<template>
  <div class="work" v-if="workData">
    <h2 class="work__title">Work</h2>
    <div class="work__list">
      <workItem
        v-for="item in workData"
        :key="(item.company.replace(' ','_'))"
        :item="item"
      />
    </div>
  </div>
</template>
<script>
import workItem from './subComponents/work-item';

export default {
  name: 'work',
  props: {
    workData: {
      type: Array,
      required: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  components: {
    workItem,
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_work.scss";
</style>



