<template>
  <div class="project" v-if="projectData">
    <h2 class="project__title">Projects</h2>
    <div class="project__list">
      <projectItem
        v-for="item in projectData"
        :key="(item.name.replace(' ','_'))"
        :item="item"
      />
    </div>
  </div>
</template>
<script>
import projectItem from './subComponents/project-item';

export default {
  name: 'project',
  props: {
    projectData: {
      type: Array,
      required: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  components: {
    projectItem,
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_project.scss";
</style>



