<template>
  <div v-if="loaded">
    <div class="card">
      <div :class="{
        'card__header--avatar' : avatar,
        'card__header' : true,
        'card__header--red' : true
      }">
        <img class="avatar avatar--absolute" v-if="avatar" :src="avatar" />
        <h1 class="card__title">{{cv.basics.name}}</h1>
      </div>

      <aboutMe :info="cv.basics" :githubInfo="githubAccountInfo"/>

      <intro :intro="cv.basics.description" />
      <education :edu="cv.education" />
      <work :workData="inverseWork" />
      <skills :skillData="cv.skills" />
      <projects :projectData="cv.projects" />
      
    </div>
    <pdfcv :cv="cv" :githubInfo="githubAccountInfo" />
  </div>
</template>

<script>
import aboutMe from './aboutMe';
import education from './education';
import work from './work';
import skills from './skills';
import projects from './projects';
import intro from './intro';
import pdfcv from './subComponents/pdf-cv';

export default {
  name: 'home',
  data() {
    return {
      cv: {},
      loaded: true,
      error: null,
      githubAccountInfo: null,
      avatar: null,
    };
  },
  created() {
    this.fetchCVJSON()
      .then(() => this.getAvatar());
  },
  methods: {
    async fetchCVJSON() {
      this.error = null;
      this.loaded = false;
      try {
        const cv = await fetch('/static/cv.json');
        this.cv = await cv.json();
        document.title = `${this.cv.basics.name}'s Digital CV`;
        this.loaded = true;
      } catch (e) {
        this.error = e;
        this.loaded = false;
      }
    },
    async getAvatar() {
      this.avatar = false;
      if (!this.loaded) return;
      try {
        const apiRequestURL = `https://api.github.com/search/users?q=${this.cv.basics.email}`;
        const githubInfo = await fetch(apiRequestURL);
        const githubInfoAsJSON = await githubInfo.json();

        this.githubAccountInfo = githubInfoAsJSON.items[0];

        this.avatar = githubInfoAsJSON.items[0].avatar_url;
      } catch (e) {
        this.error = e;
        this.avatar = false;
      }
    }
  },
  computed: {
    inverseWork() {
      if (!this.cv.work) return false;
      return this.cv.work.slice().reverse();
    },
  },
  components: {
    aboutMe,
    education,
    work,
    skills,
    projects,
    intro,
    pdfcv,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
</style>



