<template>
  <div class="skill" v-if="skillData">
    <h2 class="skill__title">Skills</h2>
    <div class="skill__list">
      <skillSection
        v-for="item in skillData"
        :key="(item.name.replace(' ','_'))"
        :item="item"
      />
    </div>
  </div>
</template>
<script>
import skillSection from './subComponents/skill-section';

export default {
  name: 'skills',
  props: {
    skillData: {
      type: Array,
      required: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  components: {
    skillSection,
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_skill.scss";
</style>



