<template>
  <div class="project__item" v-if="item.name">
    <h3 class="project__item__title">{{ item.name }}</h3>
    <div class="project__item__meta">
      <span
        class="project__item__meta__item"
        v-for="(tool,index) in item.keywords"
        :key="index"
      >
        {{ tool }}
      </span>
    </div>
    <p>
      {{item.description}}
    </p>
  </div>
</template>
<script>
export default {
  name: 'projectItem',
  props: {
    item: {
      type: Object,
      required: true,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_project.scss";
</style>



