<template>
  <div class="education" v-if="edu">
    <h2 class="education__title">Education</h2>
    <div class="education__list">
      <eduItem
        v-for="item in edu"
        :key="(item.area.replace(' ','_'))"
        :item="item"
      />
    </div>
  </div>
</template>
<script>
import eduItem from './subComponents/education-item';

export default {
  name: 'education',
  props: {
    edu: {
      type: Array,
      required: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  components: {
    eduItem,
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
  @import "@/assets/styles/components/_education.scss";
</style>



