<template>
  <div class="about-me" v-if="this.info">
    <h2 class="about-me__title">Contact Me</h2>
    <div class="about-me__inner">
      <a
        :v-if="this.info.email"
        :href="mailTo"
        class="about-me__mail about-me__icon"
      >{{ this.info.email }}</a>
      <a
        :v-if="this.info.phone"
        :href="callMe"
        class="about-me__phone about-me__icon"
      >{{ this.info.phone }}</a>
      <a
        :v-if="this.info.twitter"
        :href="this.info.twitter"
        class="about-me__twitter about-me__icon"
      >Twitter</a>
      <a
        :v-if="githubInfo"
        :href="githubInfo.html_url"
        class="about-me__github about-me__icon"
      >Github</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'aboutMe',
  props: {
    info: {
      type: Object,
      required: true,
    },
    githubInfo: {
      type: Object,
      required: false,
    }
  },
  data() {
    return {};
  },
  computed: {
    mailTo() {
      return `mailto:${this.info.email}`;
    },
    callMe() {
      return `tel:${this.info.phone}`;
    },
  },
};
</script>

<style lang="scss" scoped>
  @import "@/assets/styles/_assets.scss";
</style>



